:root {
  --primary-font: "Montserrat", sans-serif;
  --title-font: "Seymour One", sans-serif;
  --axent-colour: #f8d24e;
  --dark-font-color: #131313;
  --white-font-color: #fcfcfc;
  --background-color: #84a178;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.logo {
  z-index: 1000;
}

.page-header__container {
  background-color: var(--background-color);
  padding-top: 12px;
}

