    padding-top: 8px;
  }
}

.navigation {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

@media screen and (max-width: 1199px) {
  .navigation__list {
    display: none;
  }
}

.navigation__item:not(:last-child) {
  z-index: 100;
  margin-right: 27px;
}

.navigation__link {
  font-family: var(--primary-font);
  color: var(--white-font-color);
  align-items: center;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.25;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.navigation__link:hover, .navigation__link:focus {
