}

.button-header {
  z-index: 100;
  min-width: 152px;
  height: 42px;
  font-family: var(--primary-font);
  color: #000;
  background-color: var(--axent-colour);
  border-color: rgba(0, 0, 0, 0);
  border-radius: 10px;
  justify-content: space-between;
  align-items: center;
  padding: 12px 34px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.2;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-flex;
}

.button-header:hover, .button-header:focus {
  background-color: #0a8817;
}

.button-header:focus {
  font-size: 15px;
  font-weight: 700;
  line-height: 1.22;
}

@media screen and (max-width: 767px) {
  .button-header {
    display: none;
  }
}

.mobile-menu-open {
